body {
  position: relative;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 1;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  box-sizing: border-box;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-width: 0;
  padding: 0;
  margin: 0;
}

ol,
ul {
  list-style-type: none;
}

html {
  height: 100%;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
}

img {
  display: block;
  max-width: 100%;
}

input,
textarea {
  border: none;
  outline: none;
  color: black;
  border-radius: 0;
  -webkit-appearance: none;
}

textarea {
  resize: none;
  overflow: auto;
}

a {
  text-decoration: none;
}

.user-form__table {
  width: 600px;
  margin: 50px;
  padding: 20px;
  border: 2px solid #dee2e6;
}

.user-form__table table {
  width: 100%;
  max-width: 100%;
}

.user-form__table table th,
.user-form__table table td {
  padding: 10px;
  text-align: left;
  vertical-align: top;
  border-top: 2px solid #dee2e6;
}

.user-form__table table thead th {
  vertical-align: bottom;
  border-bottom: 4px solid #dee2e6;
}

.user-form__table table tbody + tbody {
  border-top: 4px solid #dee2e6;
}

.user-form__table table .thead-dark th {
  color: white;
  background-color: black;
  border-color: black;
}

.user-form__table table .thead-dark tr {
  border-color: black;
}

.user-form {
  display: flex;
  flex-wrap: wrap;
  margin: 20px 50px 0;
}

.user-form__inputs {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.user-form__inputs label {
  width: 18%;
}

.user-form__inputs button {
  width: 18%;
  color: white;
  background-color: #007bff;
  border-color: #007bff;
  border-radius: 5px;
  transition: all .3s;
  cursor: pointer;
  -webkit-appearance: button;
}

.user-form__inputs button:hover {
  background-color: #005cbf;
  border-color: #005cbf;
}

.user-form__search {
  width: 400px;
  margin-top: 30px;
}

.user-form input,
.user-form select {
  position: relative;
  display: block;
  width: 100%;
  padding: 15px;
  box-sizing: border-box;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 5px;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.user-form select {
  text-transform: capitalize;
}

.user-form label {
  position: relative;
}

.user-form .warning::before {
  position: absolute;
  bottom: -12px;
  left: 0;
  font-size: 12px;
  color: red;
}

.user-form .warning[data-name='firstName']::before {
  content: "Wrong first name";
}

.user-form .warning[data-name='lastName']::before {
  content: "Wrong last name";
}

.user-form .warning[data-name='email']::before {
  content: "Wrong email";
}

.user-form .warning input {
  border: 1px solid red;
}